import { Card } from "antd";
import { LoadingOutlined } from "@ant-design/icons";
import React from "react";

export default function Loader() {
  return (
    <Card >
      <div style={{ padding: 16, margin: "auto", margin: 64 }}>
        <div className="icons-list">
          <LoadingOutlined />
        </div>
      </div>
    </Card>
  );
}
