import { ControlFilled, HomeFilled, InfoCircleFilled, MenuOutlined } from "@ant-design/icons";
import { Button, Layout, Menu, PageHeader } from "antd";
import "antd/dist/antd.css";
import React, { useEffect, useState } from "react";
// import LazyLoad from "react-lazyload";
import { BrowserRouter, Link } from "react-router-dom";
import "./App.css";
import { InnerContent, FooterContent } from "./components";
// import { About, Dashboard, Help, Home } from "./views";
const { Header, Sider, Content } = Layout;

const DEBUG = 0;

function App() {
  if (DEBUG) {
    console.log();
    // console.log(_);
  }

  const [route, setRoute] = useState();
  useEffect(() => {
    setRoute(window.location.pathname);
  }, [setRoute]);

  const responsiveWidth = 1400;
  // const marginResponsive = "15%";
  const siderWidth = 290;
  const marginResponsive = Math.max(0, (window.innerWidth - responsiveWidth - siderWidth) / 2);
  // console.log(marginResponsive);
  const [isMobile, setIsMobile] = useState(
    /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
      window.innerWidth < responsiveWidth,
  );

  const [menuWidth, setMenuWidth] = useState(isMobile || window.innerWidth < responsiveWidth ? 0 : 200);
  // const menuWidth = window.innerWidth > responsiveWidth ? 200 : 0;

  const [isMobileMenuOpened, setIsMobileMenuOpened] = useState(false);

  // const [contentStart, setContentStart] = useState(isMobile && !isMobileMenuOpened ? 65 : 0);

  const [w, setW] = useState(
    window.innerWidth < responsiveWidth ? window.innerWidth - 90 - 90 : window.innerWidth - 200 - 90 - 90,
  );
  const [h, setH] = useState((w * 9) / 16);

  window.addEventListener("resize", function () {
    setW(isMobileMenuOpened || window.innerWidth >= responsiveWidth? window.innerWidth - 200 - 90 - 90 : window.innerWidth - 90 - 90);
    setH((w * 9) / 16);
    // setIsMobileMenuOpened(false);
    setIsMobile(
      /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ||
        window.innerWidth < responsiveWidth,
    );
    setMenuWidth(isMobileMenuOpened ? 200 : isMobile || window.innerWidth < responsiveWidth ? 0 : 200);
  });

  return (
    <BrowserRouter>
      <div className="App">
        <Layout>
          <Header style={{ position: "fixed", zIndex: 1, width: "100%" }}>
            {route === "/dashboard" ? (
              ""
            ) : (
              // <div style={{ zIndex: 1, position: "fixed", textAlign: "right", right: 0, top: 0, padding: 10 }}>
              // <Account
              //   address={address}
              //   ensProvider={ethereumProvider}
              //   web3Modal={web3Modal}
              //   loadWeb3Modal={loadWeb3Modal}
              //   logoutOfWeb3Modal={logoutOfWeb3Modal}
              // /></div>
              <div style={{ zIndex: 1, position: "fixed", textAlign: "right", right: 0, top: 0, padding: 10 }}>
                <Button
                  type="primary"
                  // key="loginbutton"
                  style={{ verticalAlign: "top", marginLeft: 8, marginTop: 4 }}
                  shape="round"
                  size="large"
                  /* type={minimized ? "default" : "primary"}     too many people just defaulting to MM and having a bad time */
                  onClick={() => {
                    window.location = "/dashboard";
                  }}
                >
                  <b style={{ letterSpacing: 1 }}>Launch App</b>
                </Button>
              </div>
            )}{" "}
          </Header>
        </Layout>

        <Layout>
          {isMobile ? (
            isMobileMenuOpened ? (
              <div style={{ zIndex: 1 }}>
                <PageHeader
                  avatar={{ src: "/pics/logo_coins_woodwater_large_square_64_reduced.png" }}
                  subTitle="WATERWOOD"
                  extra={[
                    <Button
                      onClick={() => {
                        setMenuWidth(0);
                        setIsMobileMenuOpened(false);
                        setW(window.innerWidth - 90 - 90);
                        setH((w * 9) / 16);
                      }}
                      icon={<MenuOutlined />}
                      type="primary"
                    ></Button>,
                  ]}
                  style={{ cursor: "pointer", position: "fixed", top: 0 }}
                />
              </div>
            ) : (
              <div style={{ zIndex: 1 }}>
                <PageHeader
                  extra={[
                    <Button
                      onClick={() => {
                        setW(window.innerWidth - 200 - 90 - 90);
                        setH((w * 9) / 16);
                        setIsMobileMenuOpened(true);
                        setMenuWidth(200);
                        
                      }}
                      icon={<MenuOutlined />}
                      type="primary"
                    ></Button>,
                    // isMobileMenuOpened ? <InnerHeaderMobile/> :""
                  ]}
                  style={{ cursor: "pointer", position: "fixed", top: 0 }}
                />
              </div>
            )
          ) : (
            <div style={{ zIndex: 1 }}>
              {" "}
              <a href="/">
                {" "}
                {/* href="https://waterwood.network" >*/}
                <PageHeader
                  avatar={{ src: "/pics/logo_coins_woodwater_large_square_64_reduced.png" }}
                  title="WATERWOOD"
                  style={{ cursor: "pointer", position: "fixed", top: 0 }}
                />
              </a>
            </div>
          )}
          <Sider
            theme="light"
            style={{
              overflow: "auto",
              height: "100vh",
              position: "fixed",
              left: 0,
              width: 200,
            }}
          >
            {/*<Menu style={{ textAlign: "center" , position:"fixed",  top:0,left: "50%", transform: "translate(-50%, 0%)", padding: 15}} selectedKeys={[route]} mode="horizontal">*/}
            {menuWidth > 100 ? (
              <Menu
                style={{ fontWeight: 550, fontSize: 15, textAlign: "left", marginLeft: 17, marginTop: 65 }}
                selectedKeys={[route]}
                mode="vertical"
              >
                <Menu.Item key="/" icon={<HomeFilled />}>
                  <Link
                    onClick={() => {
                      if (isMobile && isMobileMenuOpened) {
                        setMenuWidth(0);
                        setIsMobileMenuOpened(false);
                      }
                      setRoute("/");
                    }}
                    to="/"
                  >
                    <b style={{ letterSpacing: 1 }}>Home</b>
                  </Link>
                </Menu.Item>

                <Menu.Item key="/dashboard" icon={<ControlFilled />}>
                  <Link
                    onClick={() => {
                      if (isMobile && isMobileMenuOpened) {
                        setMenuWidth(0);
                        setIsMobileMenuOpened(false);
                      }
                      setRoute("/dashboard");
                    }}
                    to="/dashboard"
                  >
                    <b style={{ letterSpacing: 1 }}>Dashboard</b>
                  </Link>
                </Menu.Item>
                {/* <Menu.Item key="/about" icon={<StarFilled />}>
                <Link
                  onClick={() => {if (isMobile &&
                      isMobileMenuOpened){setMenuWidth(0);
                        setIsMobileMenuOpened(false);}
                    setRoute("/about");
                  }}
                  to="/about"
                >
                  <b style={{letterSpacing:1}}>About</b> 
                </Link>
              </Menu.Item> */}
                <Menu.Item key="/help" icon={<InfoCircleFilled />}>
                  <Link
                    onClick={() => {
                      if (isMobile && isMobileMenuOpened) {
                        setMenuWidth(0);
                        setIsMobileMenuOpened(false);
                      }
                      setRoute("/help");
                    }}
                    to="/help"
                  >
                    <b style={{ letterSpacing: 1 }}>Help</b>
                  </Link>
                </Menu.Item>
                {/* <Menu.Item key="/water">
            <Link
              onClick={() => {
                setRoute("/water");
              }}
              to="/water"
            >
              WATER
            </Link>
          </Menu.Item>
          <Menu.Item key="/wood">
            <Link
              onClick={() => {
                setRoute("/wood");
              }}
              to="/wood"
            >
              WOOD
            </Link>
          </Menu.Item> */}
              </Menu>
            ) : (
              ""
            )}
          </Sider>
          <Layout
            className="site-layout"
            style={{ marginLeft: menuWidth, marginTop: 65, paddingLeft: 45, paddingRight: 45 }}
          >
            {window.innerWidth < responsiveWidth ? (
              <Content>
                <InnerContent w={w} h={h} />
              </Content>
            ) : (
              <Content style={{ marginRight: marginResponsive, marginLeft: marginResponsive }}>
                <InnerContent w={w} h={h} />
              </Content>
            )}
            <FooterContent />
            {/* <Footer> */}

            {/* </Footer> */}
          </Layout>
        </Layout>

        {/* <ThemeSwitch /> */}
      </div>{" "}
    </BrowserRouter>
  );
}

export default App;
