import React from "react";
import {Route, Switch } from "react-router-dom";
import { FooterContent } from "../components";
import { About, Dashboard, Help, Home } from "../views";

// displays a page header

export default function InnerContent({w,h}) {
  return (
   
    <div>
     <Switch>
                <Route exact path="/">
                  <Home w={w} h={h}/>
                </Route>
                {/* <LazyLoad height={200} offset={100} once={true}> */}
                  <Route exact path="/dashboard">
                    {/* <LazyLoad height={200} offset={100} once={true}> */}
                      <Dashboard />
                    {/* </LazyLoad> */}
                  </Route>
                  <Route path="/about">
                    {/* <LazyLoad height={200} offset={100} once={true}> */}
                      <About />
                    {/* </LazyLoad> */}
                  </Route>
                  <Route path="/help">
                    {/* <LazyLoad height={200} offset={100} once={true}> */}
                      <Help />
                    {/* </LazyLoad> */}
                  </Route>
                {/* </LazyLoad> */}
                {/* <Route path="/water">
                  <Contract
                    name="Interact with WATER"
                    customContract={customContractWATER}
                    signer={signer}
                    provider={localProvider}
                    address={address}
                    blockExplorer={blockExplorer}
                  />
                </Route>
                <Route path="/wood">
                  <Contract
                    name="Interact with WOOD"
                    customContract={customContractWOOD}
                    signer={signer}
                    provider={localProvider}
                    address={address}
                    blockExplorer={blockExplorer}
                  />
                </Route> */}
              </Switch>
             
    </div>
  );
}
