import React from "react";
import { Card } from "antd";
import { BurnWOOD, LockWATER, MintWOOD, UnlockWATER, Loader } from "../components";

export default function GrowWOOD({ _ }) {
  return (
    <div>
      {!isNaN(_.lockedInWATER) && _.lockedInWATER !== undefined ? (
        _.lockedInWATER === 0 ? (
          <div>
            <div>
              <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}}>
                <LockWATER _={_} />
              </Card>
            </div>{" "}
            {/* <div>
              <Card style={{ marginBottom: 0 }}>
                <BurnWOOD _={_} />
              </Card>
            </div> */}
          </div>
        ) : (
          <div>
            <div>
              <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}}>
                <MintWOOD _={_} />{" "}
              </Card>
            </div>
            <div>
              <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}}>
                <UnlockWATER _={_} />{" "}
              </Card>
            </div>
            <div>
              <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}}>
                <BurnWOOD _={_} />
              </Card>
            </div>
          </div>
        )
      ) : (
        <Loader/>
      )}
    </div>
  );
}
