import React from "react";
import LottieAnimation from "./LottieAnimation";
import home from "../Lotties/Tree.json";

export default function DisplayLottie({w,h}) {

  return (
    <div className="displayLottie" style={{ width: "100%" }}>
      <LottieAnimation style={{ width: "100%" }} lotti={home} height={h} width={w} />
    </div>
  );
}
