import { Divider, Card } from "antd";
import React from "react";
import { Address } from ".";

// displays a page header

export default function APYInfo({ _ }) {
  const unMintedBlocks = _.lastMintBlockNumber ? _.lastBlockNumber - _.lastMintBlockNumber : 0;
  const unMintedDays = unMintedBlocks / 28800;
  const yourMultiplier = 10 * _.burnMultiplier;
  const yourAPY =
    unMintedDays && _.lockedInWATER
      ? (((_.WOODmintAmount / unMintedDays) * 365 * _.priceWOOD) / (_.lockedInWATER * _.priceWATER)) * 100
      : 0;
  const onexAPY = ((1 * 10 * 0.00000001 * 28800 * 365 * _.priceWOOD) / _.priceWATER) * 100;
  const fiftyxAPY = ((50 * 10 * 0.00000001 * 28800 * 365 * _.priceWOOD) / _.priceWATER) * 100;

  return (
    <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}}>
      <Divider style={{ fontSize: 40 }} plain>
        {yourAPY ? (
          <b>Your APY: {yourAPY.toFixed(2).toLocaleString()}%</b>
        ) : (
          <b> APY: {fiftyxAPY.toFixed(2).toLocaleString()}% </b>
        )}
      </Divider>
      <div>
        <div>
          {yourAPY ? (
            ""
          ) : (
            // <b>Your APY: {yourAPY.toFixed(2).toLocaleString()}%</b>
            <b>Lock-In WATER to grow WOOD</b>
          )}
        </div>
        <div>APY (1x burn bonus): {onexAPY.toFixed(2).toLocaleString()}%</div>
        <div>APY (50x burn bonus): {fiftyxAPY.toFixed(2).toLocaleString()}%</div>

        {/* <Divider />
        <div>Price WATER: ${_.priceWATER.toFixed(10)}</div>
        <div>Price WOOD: ${_.priceWOOD.toFixed(10)}</div> */}
      </div>
    </Card>
  );
}
