import { Divider, Card } from "antd";
import React from "react";
// displays a page header

export default function TokenInfo({ _ }) {
  return (
    <Card style={{"background-color":_.STYLE.COLORCARD[0], marginTop:_.STYLE.MARGINCARD.top,marginBottom:_.STYLE.MARGINCARD.bottom}} title={<b>MY INFO</b>}>
      <b><h3>    <b>  <div><b>
        <div><b>WATER Balance: {_.WATERBalance.toLocaleString()}</b></div>
        <div><b>WOOD Balance: {_.WOODBalance.toLocaleString()}</b></div>
        <Divider />
        <div>WOOD Being Grown: {_.WOODmintAmount.toLocaleString()}</div>
        <div>Locked-In WATER: {_.lockedInWATER.toLocaleString()}</div>
        <div>Burned WOOD: {_.burnedWOOD.toLocaleString()}</div>
        <div>Burn Ratio: {(_.myRatio * 100).toLocaleString()}%</div>
        <div>Burn Multiplier: {_.burnMultiplier.toLocaleString()}x</div>
        <div>Time Multiplier: {_.timeMultiplier.toLocaleString()}x</div>
        </b>   </div></b></h3></b>
    </Card>
  );
}
